import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, Events, AlertController } from 'ionic-angular';
import { RestProvider } from "../../providers/rest/rest";
import { CartProvider } from "../../providers/cart/cart";


@IonicPage({name:'Cart'})
@Component({
  selector: 'page-cart',
  templateUrl: 'cart.html',
})
export class CartPage {

  offerSuccessMsg:any;
  offerFailureMsg:any;
  offerMsg:any;
  localCartProducts:any;
  cartItems:any;
  grandTotal:any;
  existCartProdductNumber:any;
  totalQty:any;


cartLength:any;
totalamt:any;

taxArray=[];

taxGroup:any;
taxGroups=[];
cartTax=[];
totaltax:any;
totalbill=0;
result_data:any;

  constructor(public cartprvdr:CartProvider, public navCtrl: NavController,public restprovider:RestProvider, public navParams: NavParams, public event:Events, public alertCtrl: AlertController) {



    this.localCartProducts=this.cartprvdr.localCartProducts;
    

    // if(this.localCartProducts !=null || this.localCartProducts !=undefined ){
    //   this.cartItems=this.localCartProducts.length;   
  
    //   }else{

    //     console.log(1)
    //   }
      this.priceTotal();
 
  


      // this.final_TaxAmount1();
   

  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad CartPage');
      this.itemtaxes();  
        
      this.cartTaxInfo();
      this.priceTotal();

  }

  checkoutPay(){
    this.navCtrl.push('Paymentmethod');
  }
  couponVerify(offerCode){
    this.offerSuccessMsg='Offer Applied';
    this.offerFailureMsg='Invalid Coupon';
    console.log(offerCode)
    if(offerCode == undefined){
      this.offerSuccessMsg=0;
      this.offerFailureMsg='Invalid Coupon';
    }else{
      this.offerSuccessMsg='Offer Applied, 10Rs Discount';
    this.offerFailureMsg=0;

    }

  }


  // increment product qty
incrementQty(product) {

  this.localCartProducts=this.cartprvdr.localCartProducts;
  if(this.localCartProducts !=null || this.localCartProducts !=undefined ){
  this.existCartProdductNumber= this.localCartProducts.filter(function (element) {
    return (element.ProductNumber == product.ProductNumber);
    }); 
  }
    var cartItem=this.existCartProdductNumber;        
    cartItem[0].productQty++;
    cartItem[0].subTotal=(parseFloat(cartItem[0].productQty)*parseFloat(cartItem[0].Price1));

// console.log(product.qty)
  // localStorage.setItem('cartitem',JSON.stringify(this.localCartProducts));
this.cartprvdr.localCartProducts=  this.localCartProducts;
  this.itemtaxes();  
        
  this.cartTaxInfo();
  this.priceTotal();

  }
  
  // decrement product qty
  decrementQty(product,index) {

// console.log(product)
    this.localCartProducts=this.cartprvdr.localCartProducts;
    if(this.localCartProducts !=null || this.localCartProducts !=undefined ){
    this.existCartProdductNumber= this.localCartProducts.filter(function (element) {
      return (element.ProductNumber == product.ProductNumber);
      }); 
    }
      var cartItem=this.existCartProdductNumber;
    
      cartItem[0].productQty--;
  
    if(cartItem[0].productQty < 1){
      const filteredItems = this.localCartProducts.filter((item) => item.ProductNumber !== cartItem[0].ProductNumber);

      this.localCartProducts=filteredItems;

      this.cartprvdr.localCartProducts=this.localCartProducts
      // localStorage.setItem('cartitem',JSON.stringify(filteredItems));
    }
  

        // console.log(product.qty)
        this.cartprvdr.localCartProducts=this.localCartProducts
    // localStorage.setItem('cartitem',JSON.stringify(this.localCartProducts));
    this.itemtaxes();  
        
    // this.cartTaxInfo();
    this.priceTotal();
  }




  cartlength(){    
    // this.localCartProducts=JSON.parse(localStorage.getItem('cartitem'));
    this.localCartProducts=this.cartprvdr.localCartProducts;
  if(this.localCartProducts !=null || this.localCartProducts !=undefined ){
    this.cartLength=this.localCartProducts.length;
  }else{
    this.cartLength=0;
  }
}

priceTotal(){
  this.totalamt=0;
  this.totalQty=0
// this.localCartProducts=JSON.parse(localStorage.getItem('cartitem'));
this.localCartProducts=this.cartprvdr.localCartProducts;

if(this.localCartProducts !=null || this.localCartProducts !=undefined ){

  
for (let item of this.localCartProducts) {
  this.totalamt = (this.totalamt) + (item.productPrice*item.productQty);
}
var taxinit=0;

// console.log('t', this.cartTax);

for (let taxamnt of this.cartTax){ 

taxinit = (taxinit) + (taxamnt.taxprice);
}

this.totaltax=taxinit;

this.grandTotal= (this.totalamt).toFixed(2);


this.totalbill= (+this.grandTotal+this.totaltax).toFixed(2);



}else{

  this.grandTotal=0.00;
}
}


itemtaxes(){

// this.localCartProducts=JSON.parse(localStorage.getItem('cartitem'));
this.localCartProducts=this.cartprvdr.localCartProducts;


if(this.localCartProducts !=null || this.localCartProducts !=undefined ){
  
for (let item of this.localCartProducts) {

  for (let iTax of item.Tax){

  if(item.Tax.find(x => x.tax_id == iTax.tax_id)){
this.taxArray.push({
  'tax_id':iTax.tax_id,
  'taxdes':iTax.taxdes,
  'taxper':iTax.taxper,
  'taxprice':iTax.taxprice
})
    // console.log(this.taxArray);
  }else {
    console.log('2',iTax);
  }


}
}


for (let tax of this.taxArray){    
  this.taxGroup= this.taxArray.filter(function (element) {
    return (element.tax_id == tax.tax_id);
    }); 
    this.taxGroups.push(this.taxGroup);
  }

}

}


cartTaxInfo(){
  
  if(this.localCartProducts !=null || this.localCartProducts !=undefined ){
  for (let tax of this.taxArray){    

    this.taxGroup= this.taxArray.filter(function (element) {
      return (element.tax_id == tax.tax_id);
      }); 
    
      this.taxGroups.push(this.taxGroup);
    }
  

    for (let a of this.taxGroup){ 
      if (!this[a.tax_id]) {
          this[a.tax_id] = { tax_id: a.tax_id,taxdes: a.taxdes,taxper:a.taxper, taxprice: 0 };
          this.cartTax.push(this[a.tax_id]);
      }
      this[a.tax_id].taxprice += a.taxprice;
  }
  console.log('test', this.cartTax)
    
  }

}

removeCartItem(product){


  // this.localCartProducts=JSON.parse(localStorage.getItem('cartitem'));
this.localCartProducts=this.cartprvdr.localCartProducts;

const filteredItems = this.localCartProducts.filter((item) => item.ProductNumber !== product.ProductNumber);
console.log(filteredItems)
this.localCartProducts=filteredItems;

this.cartprvdr.localCartProducts=this.localCartProducts

// localStorage.setItem('cartitem',JSON.stringify(filteredItems));

this.itemtaxes();  
        
this.cartTaxInfo();
this.priceTotal();

}


checkoutorder(){

  // this.localCartProducts=JSON.parse(localStorage.getItem('cartitem'));
  this.localCartProducts=this.cartprvdr.localCartProducts;

  const data={'store_id':this.localCartProducts[0].store_id}
  this.restprovider.postapidata(data).subscribe((data:any)=>{
    this.result_data=data.data;  

    console.log(data)
    
    let alert = this.alertCtrl.create({
      title: 'Thank you',
      subTitle: 'Order Placed',
      buttons: ['Dismiss']
    });
    alert.present();

    this.navCtrl.push('Paymentmethod');
  })
}










// pageclass end here

}














